@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header .content {
    display: flex;
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 25px;
}

.header .gocHeaderLogo {
    width: 250px;
    object-fit: contain;
    margin-right: 20px;
    align-self: center;
}

.header .titleBox {
    display: flex;
}

.header .titleBox a {
    text-decoration: none;
}

.header h1 {
    font-size: 1.625rem;
    margin: 0;
}

.header .alpha {
    background-color: #b2e3ff;
    font-size: 1rem;
    margin: 5px 12px;
    padding: 3px 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
}

.header .languageToggle {
    align-self: center;
    color: #26374a;
    margin-left: auto;
}

@media only screen and (max-width: 1000px) {

    .header .content {
        flex-direction: column;
        padding: 15px 25px;
        margin: unset;
        box-sizing: border-box;
    }

    .header .gocHeaderLogo {
        order: 1;
        align-self: start;
    }

    .header .titleBox {
        margin-top: 25px;
        margin-bottom: 12px;
        order: 1;
    }

    .header .languageToggle {
        align-self: start;
        margin-left: unset;
        margin-bottom: 25px;
    }

}
.bannerBlock {
    padding: 3rem 130px;
}

.bannerBlock .content {
    display: flex;
    justify-content: center;
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
}

.bannerBlock img {
    width: 350px;
    margin-left: 25px;
    object-fit: contain;
    align-self: center;
}

@media only screen and (max-width: 1000px) {

    .bannerBlock {
        padding: 1rem 0;
    }

    .bannerBlock .content {
        width: 95%;
    }

    .bannerBlock img {
        display: none;
    }
}
.navButton {
    background-color: #b2e3ff;
    color: black;
    width: auto;
    display: inline-block;
    padding: 10px 12px;
}

div .navButton:nth-of-type(n + 2){
    margin-left: 10px;
}

.navButton a {
    text-decoration: none;
}

.navButton p {
    margin: 0;
}

@media only screen and (max-width: 1000px) {

    .navButton {
        margin-bottom: 15px;
    }

}
.catalog .title {
    text-align: center;
}

.catalog .collection {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.catalog .resource {
    width: 30%;
    margin: 15px 0;
}

.catalog img {
    max-width: 100%;
    border: 2px solid gray;
    padding: 10px;
    border-bottom-left-radius: 25px;
    box-sizing: border-box;
}

.catalog figcaption {
    margin-top: 10px;
}

.catalog p {
    font-size: 1rem;
    overflow-wrap: break-word;
}

@media only screen and (max-width: 750px) {

    .catalog .resourceType {
        text-align: center;
    }

    .catalog .collection {
        justify-content: center;
    }

    .catalog .resource {
        width: auto;
        max-width: 300px;
    }

}
body {
    font-family: 'Lato', sans-serif;
}

h2 {
    font-size: 2.25rem;
}

p {
    font-size: 1.25rem;
}

li {
    font-size: 1.25rem;
}

a {
    color: inherit;
}

figcaption {
    font-size: 0.625rem;
}

.markdown, .contentBox {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
}
